
.container{
    max-width: 400px;
    margin: 50px auto 0 auto;
    box-shadow: 0px 0px 40px 20px rgb(240, 240, 149);
}
#display {
    text-align: right;
    height: 70px;
    line-height: 70px;
    padding: 16px 8px;
    font-size: 25px;
    background-color: rgb(189, 192, 189);

}
.buttons {
    display: grid;
    border-bottom: 1px solid #999;
    border-left: 1px solid#999;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.button {
    border: 2px solid #999;
    line-height: 100px;
    text-align: center;
    font-size: 25px;
    cursor: pointer;
    background-color: rgb(15, 15, 15);
    color: white;
}
#equal {
    background-color: rgb(248, 21, 21);
    color: white;
}
.button:hover {
    background-color: #eceee8;
    color: rgb(15, 12, 12);
    transition: 0.5s ease-in-out;
}
